// Bu istee bal kod service worker kullanarak uygulama ieriinizi daha hzl 
// yklemeye, ayrca evrimd yetenekler de eklemeye yardmc olur.
// Ancak bu yaklam baz tuzaklar da beraberinde getirir; service worker'larn
// yaam dngs tam olarak anlalmazsa alma zaman hatalarna neden olabilir.

const isLocalhost = Boolean(
  window.location.hostname === 'localhost' ||
    // [::1] localhost iin IPv6 adresidir.
    window.location.hostname === '[::1]' ||
    // 127.0.0.0/8 localhost iin zel IPv4 adresi kabul edilir.
    window.location.hostname.match(/^127(?:\.(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)){3}$/)
);

export function register(config) {
  if (process.env.NODE_ENV === 'production' && 'serviceWorker' in navigator) {
    // URL constructor, SW'nin https sayfalarnda almasn salar
    const publicUrl = new URL(process.env.PUBLIC_URL, window.location.href);
    if (publicUrl.origin !== window.location.origin) {
      // PUBLIC_URL farkl bir originden geliyorsa, service worker almaz
      return;
    }

    window.addEventListener('load', () => {
      const swUrl = `${process.env.PUBLIC_URL}/service-worker.js`;

      if (isLocalhost) {
        // Localhost'tayz. SW'nin hala var olup olmadn kontrol edelim
        checkValidServiceWorker(swUrl, config);

        // Console'a ek bilgiler ekle
        navigator.serviceWorker.ready.then(() => {
          console.log(
            'Bu web uygulamas ilk nce nbellee alnmakta ve ' +
              'daha sonra service worker tarafndan sunulmaktadr.'
          );
        });
      } else {
        // Localhost deiliz. Sadece service worker' kaydet
        registerValidSW(swUrl, config);
      }
    });
  }
}

function registerValidSW(swUrl, config) {
  navigator.serviceWorker
    .register(swUrl)
    .then((registration) => {
      registration.onupdatefound = () => {
        const installingWorker = registration.installing;
        if (installingWorker == null) {
          return;
        }
        installingWorker.onstatechange = () => {
          if (installingWorker.state === 'installed') {
            if (navigator.serviceWorker.controller) {
              // Bu noktada nceden nbellee alnm ierik vardr
              console.log(
                'Yeni ierik kullanlabilir, ltfen sayfay yenileyin.'
              );

              // Callback'i ar
              if (config && config.onUpdate) {
                config.onUpdate(registration);
              }
            } else {
              // Bu noktada her ey nbellee alnmtr
              console.log('erik evrimd kullanm iin nbellee alnd.');

              // Callback'i ar
              if (config && config.onSuccess) {
                config.onSuccess(registration);
              }
            }
          }
        };
      };
    })
    .catch((error) => {
      console.error('Service worker kayd srasnda hata olutu:', error);
    });
}

function checkValidServiceWorker(swUrl, config) {
  // Service worker'n bulunup bulunmadn kontrol et
  fetch(swUrl, {
    headers: { 'Service-Worker': 'script' },
  })
    .then((response) => {
      // JS dosyasnn alnabildiinden emin ol
      const contentType = response.headers.get('content-type');
      if (
        response.status === 404 ||
        (contentType != null && contentType.indexOf('javascript') === -1)
      ) {
        // Service worker bulunamad. Muhtemelen farkl bir uygulama.
        // Sayfay yenile.
        navigator.serviceWorker.ready.then((registration) => {
          registration.unregister().then(() => {
            window.location.reload();
          });
        });
      } else {
        // Service worker bulundu. Normal ekilde devam et.
        registerValidSW(swUrl, config);
      }
    })
    .catch(() => {
      console.log('nternet balants yok. Uygulama evrimd modda alyor.');
    });
}

export function unregister() {
  if ('serviceWorker' in navigator) {
    navigator.serviceWorker.ready
      .then((registration) => {
        registration.unregister();
      })
      .catch((error) => {
        console.error(error.message);
      });
  }
} 