// sim bilgisi alma fonksiyonu
export const getNameInfo = (name, language = 'tr') => {
  if (!name || typeof name !== 'string') {
    return { 
      error: language === 'tr' ? 'Geersiz isim' : 'Invalid name',
      numerology: { number: 0, meaning: '' }
    };
  }

  // smi normalize et
  const normalizedName = name.trim().toLowerCase();
  
  if (normalizedName.length === 0) {
    return { 
      error: language === 'tr' ? 'sim bo olamaz' : 'Name cannot be empty',
      numerology: { number: 0, meaning: '' }
    };
  }
  
  // lk harfi al
  const firstLetter = normalizedName[0];
  // Trke karakterler iin normalizasyon
  const letterMap = { '': 'i', '': 'o', '': 'u', '': 'g', '': 's', '': 'c' };
  const normalizedLetter = letterMap[firstLetter] || firstLetter;
  
  // smin saysal deerini hesapla (basit numeroloji)
  const letterValues = {
    'a': 1, 'b': 2, 'c': 3, 'd': 4, 'e': 5, 'f': 6, 'g': 7, 'h': 8, 'i': 9,
    'j': 1, 'k': 2, 'l': 3, 'm': 4, 'n': 5, 'o': 6, 'p': 7, 'q': 8, 'r': 9,
    's': 1, 't': 2, 'u': 3, 'v': 4, 'w': 5, 'x': 6, 'y': 7, 'z': 8, '': 9,
    '': 6, '': 3, '': 7, '': 1, '': 3
  };
  
  // Girilen ismin her harfini doru saysal deerlere dntr ve topla
  let nameSum = 0;
  for (let char of normalizedName) {
    // Boluklar atla
    if (char === ' ') continue;
    // Karakter deerini letterValues'dan al, yoksa 0 ekle
    nameSum += letterValues[char] || 0;
  }
  
  // Sayy tek basamaa indir (1-9 aras)
  while (nameSum > 9) {
    let sum = 0;
    while (nameSum > 0) {
      sum += nameSum % 10;
      nameSum = Math.floor(nameSum / 10);
    }
    nameSum = sum;
  }

  // Eer sonu 0 ise (hibir harf tannmadysa), 1 olarak belirle
  if (nameSum === 0) {
    nameSum = 1;
  }
  
  // Numeroloji anlamn bul
  const meaning = numerologyMeanings[nameSum] ? 
    (language === 'tr' ? numerologyMeanings[nameSum].tr : numerologyMeanings[nameSum].en) : 
    (language === 'tr' ? 'Bilinmeyen' : 'Unknown');
  
  // Karakter zelliklerini belirle
  // Eer harfin zellii bulunamazsa, varsaylan bir deer kullan
  const firstLetterCharacteristic = nameCharacteristics[firstLetter] || 
    nameCharacteristics[normalizedLetter] || 
    (language === 'tr' ? 
      "zgn ve yaratc kiilik zellikleri gsterir" : 
      "Shows unique and creative personality traits");
  
  return {
    name: normalizedName,
    firstLetter: firstLetter,
    numerology: {
      number: nameSum,
      meaning: meaning
    },
    characteristics: firstLetterCharacteristic
  };
};

